import { useState } from 'react'
import css from './ContactsForm.module.scss'
import classnames from 'classnames'
import { useForm, Controller } from 'react-hook-form'
import Input from 'components/Atoms/Input/Input'
import Button, { ButtonHeights, ButtonPalettes } from 'components/Atoms/Button/Button'
import Radio from 'components/Atoms/Radio/Radio'
import IconSubmitted from 'assets/icons/IconSubmitted'

function ContactsForm ({
  className,
  namePlaceholder,
  phonePlaceholder,
  radioLabel,
  buttonLabel
}) {
  const DEFAULT_STATE = {
    isLoading: false,
    isLoaded: false,
    isError: false
  }

  const [mailingStatus, updateMailingStatus] = useState(DEFAULT_STATE)
  const {
    isLoading,
    isLoaded,
    isError
  } = mailingStatus
  const {control, handleSubmit, formState, reset} = useForm()
  const { errors } = formState
  const isFormError = Object.entries(errors).length > 0

  function submitForm (data) {
    updateMailingStatus(prevState => ({
      ...prevState,
      isLoading: true
    }))

    const {
      'contact-name': name,
      'contact-phone': phone,
    } = data

    const formData = new FormData()
    formData.append(`name`, name)
    formData.append(`phone`, phone)

    reset({
      'contact-name': ``,
      'contact-phone': ``,
      'contact-agree': false
    })
    updateMailingStatus(prevState => ({
      ...prevState,
      isLoading: false,
      isLoaded: true
    }))

    // fetch(`mail.php`, {
    //   method: `POST`,
    //   body: formData
    // })
    //   .then(() => {
    //     updateMailingStatus(prevState => ({
    //       ...prevState,
    //       isLoading: false,
    //       isLoaded: true
    //     }))
    //   })
    //   .catch(() => {
    //     updateMailingStatus(prevState => ({
    //       ...prevState,
    //       isLoading: false,
    //       isError: true
    //     }))
    //   })
  }

  function repeatRequest (evt) {
    evt.preventDefault()
    updateMailingStatus(DEFAULT_STATE)
  }

  function renderContent () {
    const isDefaultState = !isLoaded && !isError

    if (isDefaultState) {
      return (
        <>
          <Controller
            render={({
              field: {ref, ...props},
                fieldState: { error }
              }) => (
              <Input
                placeholder={namePlaceholder}
                formRef={ref}
                error={error && `Это поле должно быть заполнено`}
                {...props}
              />
            )}
            name={ `contact-name` }
            defaultValue=""
            control={control}
            rules={{
              required: true
            }}
          />
          <Controller
            render={({
              field: {ref, ...props},
              fieldState: { error }
            }) => (
              <Input
                placeholder={phonePlaceholder}
                formRef={ref}
                error={error && `введите корректный номер по форме +7 XXX XXX XX XX`}
                {...props}
              />
            )}
            name={ `contact-phone` }
            control={control}
            defaultValue=""
            rules={{
              required: true,
              validate: value => !Number.isNaN(+value) && value.length >= 10
            }}
          />
          <Controller
            render={({
              field: {ref, ...props}
            }) => (
              <Radio
                label={radioLabel}
                className={css.checked}
                formRef={ref}
                {...props}
              />
            )}
            name={ `contact-agree` }
            defaultValue={false}
            control={control}
            rules={{
              required: true
            }}
          />
          <Button
            className={css.button}
            height={ButtonHeights.STANDARD}
            palette={ButtonPalettes.DARK}
            isDisabled={isFormError || isLoading}
            type="submit"
          >
            { buttonLabel }
          </Button>
        </>
      )
    }

    if (isLoaded) {
      return (
        <>
          <IconSubmitted className={css.iconSubmitted} />
          <p className={css.confirmation}>
            Ваша заявка отправлена
          </p>
          <p className={css.description}>
            Мы&nbsp;приняли вашу заявку<br />и&nbsp;позвоним вам, как только сможем
          </p>
          <Button
            onClick={repeatRequest}
            className={css.buttonRepeat}
            height={ButtonHeights.STANDARD}
            palette={ButtonPalettes.DARK}
            type="button"
          >
            Заказать звонок еще раз
          </Button>
        </>
      )
    }
  }

  return (
    <form onSubmit={handleSubmit(submitForm)}>
      <div className={classnames(css.form, className)}>
        { renderContent() }
      </div>
    </form>
  )
}

export default ContactsForm
