import css from './SliderVideo.module.scss'
import classnames from 'classnames'
import SwiperCore, { Navigation, Lazy } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'

SwiperCore.use([Navigation, Lazy])

function SliderVideo ({
  className,
  list,
  setControlledSwiper
}) {
  function renderSlides () {
    return list.map((video, index) => (
      <SwiperSlide key={index}>
        <video
          className={classnames(css.video, {
            'swiper-lazy': index !== 0
          })}
          data-src={video}
          muted={true}
          loop
          playsInline
          autoPlay
        />
      </SwiperSlide>
    ))
  }

  function playVideo (video) {
    return function handlePlayVideo () {
      video.defaultMuted = true
      video.muted = true
      video.play()
      video.removeEventListener(`loadeddata`, handlePlayVideo)
    }
  }

  return (
    <Swiper
      className={classnames(css.sliderVideo, className)}
      slidesPerView={1}
      allowTouchMove={false}
      preloadImages={true}
      lazy={{
        checkInView: true,
        loadPrevNext: true,
        loadPrevNextAmount: 2,
        loadOnTransitionStart: true,
      }}
      onInit={swiper => {
        const video = swiper.slides[0].firstElementChild
        video.defaultMuted = true
        video.muted = true
        const playVideoHandler = playVideo(video)
        setControlledSwiper(swiper)
        video.src = video.dataset.src
        video.addEventListener(`loadeddata`, playVideoHandler)
      }}
      onActiveIndexChange={swiper => {
        const activeIndex = swiper.activeIndex
        const video = swiper.slides[activeIndex].firstElementChild
        video.defaultMuted = true
        video.muted = true
        video.pause()
        video.currentTime = 0
        video.play()
      }}
    >
      { renderSlides() }
    </Swiper>
  )
}

export default SliderVideo
